<!-- 历史记录页 -->
<template>
    <!-- 顶部 -->
    <TopCmp :topTitle="title" />

    <article class="main under-top-bar">
        <div class="mod-his">
            <!-- 登陆提示 -->
            <section :class="{ 'his-normal': logined }">
                <!-- <section class="his-normal"> -->
                <div class="his-bgLayBox">
                    <div class="his-login">
                        <span>登录同步后可展示云书架中的漫画</span>
                        <a class="his-empty-login" @click="login">登录</a>
                    </div>
                </div>
            </section>

            <!--头部tab切换部分 begin-->
            <section class="bookShelf-tab under-top-bar-fixed" v-if="this.historyList.length > 0" >
                <div class="tab-list">
                    <!-- onEdit 开启编辑模式 -->
                    <a id="tab_edit" :class="{ 'tab-edit': true, 'onEdit': onEdit }" @click="chooseEdit"></a>
                </div>
            </section>
            <!-- 数据部分 -->
            <section class="his-normal  " style="display: block;">
                <ul class="his-list" id="his_list">
                    <!-- choose 选择 unChoose 不选择  没有就不选择-->
                    <li :class="['comic-item', item.choose]" v-for="item in historyList" :key="item.id">
                        <router-link :to="'/read/' + item.id + '/' + item.seq" class="comic-link">
                            <div class="comic-cover">
                                <img class="cover-image" :src="item.cover" :alt="item.artist_name">
                            </div>
                            <div class="comic-info">
                                <strong class="comic-title">{{ item.title }}</strong>
                                <small class="comic-author">作者：{{ item.artist_name || item.artist }}</small>
                                <small class="comic-update">更新到{{ item.lated_seqno || '...' }}话</small>
                                <div class="comic-continue">
                                    <i class="continue-img"></i>
                                    <small>续看第{{ item.seq }}话</small>
                                </div>
                            </div>
                        </router-link>

                        <div class="comic-choose " @click="selectOne(item)">
                            <i class="icon-choose"></i>
                            <i class="icon-unChoose"></i>
                        </div>
                    </li>
                </ul>
            </section>

            <!--编辑状态下的工具栏 begin-->
            <div class="mod-edit" id="his_edit" v-bind:style="{ 'display': onEdit ? '' : 'none' }">
                <!-- 全选后 delAll -->
                <a :class="{ 'edit-choose': true, 'delAll': delAll }" id="his_edit_choose">
                    <i class="edit-choose-normal"></i>
                    <i class="edit-choose-all"></i>
                    <span v-if="delAll" @click="selectAll(false)">取消全选</span>
                    <span v-if="!delAll" @click="selectAll(true)">全选</span>
                </a>
                <a class="edit-del" id="his_edit_del" @click="delBtn">
                    <i class="edit-del-normal"></i>
                    <i class="edit-del-empty"></i>
                    <span>删除</span>
                </a>
            </div>
        </div>


    </article>


    <!-- 提示组件 -->
    <TipCmp ref="tip" />
    <!-- 返回顶部 -->
    <BackToTopCmp />
</template>
<script>

import TopCmp from "../common/TopCmp.vue"
import TipCmp from "../common/ListTipCmp.vue"
import BackToTopCmp from "../common/BackToTopCmp.vue";
import apiUtils from "../utils/ApiUtil.vue";
import historyUtil from "../utils/HistoryUtil.vue";
export default {
    data() {
        return {
            title: "历史记录",
            historyList: [],
            //编辑模式
            onEdit: false,
            //全选了
            delAll: false,
            //是否登陆， 
            logined: apiUtils.checkLogin()

        }
    },
    methods: {
        async getComicData(page = 1 ) {

            //已经达到最大页了
            if (!this.$refs.tip.canScroll()) {
                return;
            }
            // //设置动效
            this.$refs.tip.loadingStart();
            try {
                var response = null;

                response = historyUtil.getHistory();
                console.log(response)

                this.historyList = this.historyList.concat(response.data);
                this.currentPage = page;
                //是否最大页
                this.$refs.tip.loadingEnd(this.currentPage == response.maxPage, this.historyList.length > 0)
            } catch (error) {
                this.$refs.tip.hasError();
            }

        },
        handleScroll() {
            const scrollHeight = document.body.scrollHeight;
            const scrollTop = document.body.scrollTop;
            const clientHeight = document.body.clientHeight;
            if (scrollTop + clientHeight >= scrollHeight) {
                this.getComicData(this.currentPage + 1);
            }
        }
        , chooseEdit() {
            //进入或者退出编辑模式
            this.onEdit = !this.onEdit;
            //是否全选
            this.delAll = this.queryDelAll();
            //进入编辑模式
            this.enterEditMode();
        }
        , queryDelAll() {
            //数据列表是否全选了
            for (let index = 0; index < this.historyList.length; index++) {
                const element = this.historyList[index];
                if (element.choose != "choose") {
                    return false;
                }
            }
            return true;
        },
        enterEditMode() {
            //进入编辑模式
            for (let index = 0; index < this.historyList.length; index++) {
                const element = this.historyList[index];
                element.choose = this.onEdit ? "unChoose" : "";
            }
        },
        selectAll(selected) {
            //全选
            for (let index = 0; index < this.historyList.length; index++) {
                const element = this.historyList[index];
                element.choose = selected ? "choose" : "unChoose";
            }
            this.delAll = selected;
        },
        selectOne(element) {
            //选择或者取消选择一条数据
            element.choose = element.choose == "choose" ? "unChoose" : "choose";
            this.delAll = this.queryDelAll();
        }
        , delBtn() {
            //删除按钮
            for (let i = 0; i < this.historyList.length; i++) {
                if (this.historyList[i].choose === "choose") {
                    historyUtil.delHistory(this.historyList[i]);
                    this.historyList.splice(i, 1);
                    i--;
                }
            }
            //放入缓存中
            //同步到后台
        },
        login() {
            this.$router.push("/login")
        }


    },
    components: {
        TopCmp, TipCmp, BackToTopCmp
    }, mounted() {
        this.$setMetaData("历史记录");
        this.getComicData(1);
        window.addEventListener('scroll', this.handleScroll);
    },
    unmounted() {
        window.removeEventListener('scroll', this.handleScroll);
    },
}
</script>
<style scoped>
/* @import '../../assets/css/bookshelf.css'; */

.main .mod-his .his-normalfuck {
    background-color: #F5F5EE;

    padding-top: 1px;
    padding-bottom: 1px;
}
</style>