<!-- 用户中心 -->
<template>
    <!-- 顶部 -->
    <TopCmp :topTitle="title" />
    <!-- 加载中 -->
    <LoadingCmp ref="loading" />

    <article class="main">
        <!--布局上的头部，包括 mod-nav，mod-head，mod-stratRead，mod-tab-->
        <div class="lay-head">
            <section class="mod-head">
                <div class="head-banner">
                    <img :src="icon" class="head-cover" alt="">
                </div>
            </section>
        </div>

        <div class="lay-content">
            <!-- 作品详细信息 -->
            <section class="head-info">
                <div class="head-info-detail">
                    <div class="head-title-tags">
                        <h1>{{ title }}</h1>
                        <div class="head-tags">
                            <span v-for="tag in tags" :key="tag">{{ tag }}</span>
                        </div>
                    </div>
                    <div class="head-info-grade">
                        <p class="info-grade">{{ grade }}</p>
                        <div class="info-star">
                            <div class="star-wrap">
                                <div class="star-light"></div>
                                <div class="star"></div>
                            </div>
                        </div>
                        <p class="info-member-rate">{{ memberRate }}人评分</p>
                    </div>
                </div>
                <div class="head-info-desc">
                    {{ desc }}
                </div>
                <div class="head-info-author">
                    <span class="author">作者</span>
                    <ul class="author-list">
                        <li class="author-wr" v-for="author in authors" :key="author.name">
                            <img :src="author.avatar" alt="">
                            {{ author.name }}
                        </li>
                    </ul>
                </div>
            </section>



            <!-- 广告 -->
            <section class="lay-guiding">
                <div class="app-guiding gui-content">
                    <!-- <img class="app-guiding-img" src="static/picture/app-guiding.png" /> -->
                </div>
            </section>
            <!-- 中间部分的目录 -->
            <section class="chapter-info">
                <h1 class="mod-chapter-title">
                    目录
                    <span>
                        {{ status }}，已更新{{ updatedChapter }}话 (可试读至{{ tryReadChapter }}话)
                    </span>
                    <span class="chapter-more" @click="toggleChapterList"> 展开目录 <i class="icon-arrow-right"></i>
                    </span>
                </h1>

                <div class="mod-chapter-list">
                    <ul class="chapter-list chapter-horizonal" ref="modelChapterList">
                        <li v-for="chapter in chapters" :key="chapter.dataCid" class="chapter-item"
                            :class="{ 'now': chapter.seq == seq }">
                            <router-link :to="'/read/' + id + '/' + chapter.seq" class="chapter-link"
                                :class="{ 'now': chapter.seq == seq }">
                                <img :src="chapter.image"
                                    :class="{ 'chapter-img': true, 'lock': chapter.lock == 1, 'unLock': chapter.lock == 2 }"
                                    alt="">
                                <p class="chapter-title">{{ chapter.title }} </p>
                            </router-link>
                        </li>
                        <!-- 查看更多按钮 -->
                        <li class="chapter-special-li">
                            <div class="icon-show-more" id="icon_show_more" @click="toggleChapterList"></div>
                        </li>
                    </ul>
                </div>
            </section>

            <section class="recomment-list">
                <!-- 近期热门推荐 -->
                <HotComicSixCmp />
            </section>
        </div>

    </article>

    <!--底部工具栏 begin-->
    <section class="mod-toolBar">
        <div class="lay-flex">
            <div class="lay-box">
                <a id="btn_index" class="toolBar-toolIndex" href="/">
                    <i class="icon-index">[前往书库]</i>
                    <p class="iconfont" @click="home">返回主页</p>
                </a>
                <a id="btn_toolFav" v-bind:class="{ 'toolBar-toolFav': true, 'unfav': !login, 'faved': login }"
                    @click="collection">{{ collectionName }}</a>
            </div>
            <router-link :to="'/read/' + id + '/1'" class="toolBar-toolRead" v-if="this.seq <= 1">开始阅读</router-link>
            <router-link :to="'/read/' + id + '/' + this.seq" class="toolBar-toolRead" v-if="this.seq > 1">续看{{
                seq
            }}话</router-link>
        </div>
    </section>
    <!--底部工具栏 end-->

    <!-- 返回顶部按钮 -->
    <BackToTopCmp />

    <!-- 底部的目录页 show animate -->
    <section :class="{ 'bottom-chapter-list': true, 'show': isShow, 'animate': isAnimate }">
        <div class="bottom-chapter-mask"></div>
        <div class="bottom-chapter-wrap">
            <div class="wrap-top">
                <h2>目录 <span class="bottom-list-close-btn" @click="toggleChapterList"><i></i></span></h2>
                <div class="wrap-header">
                    共{{ updatedChapter }}话 (可试读至{{ tryReadChapter }}话)
                    <div class="asc-desc">
                        <span :class="{ 'active': sortAsc }" id="order_asc" @click="sort">正序</span> | <span
                            :class="{ 'active': !sortAsc }" @click="sort" id="order_desc">倒序</span>
                    </div>
                </div>
            </div>
            <span class="bottom-back-to-top bottom-icon to-top" @click="goTop"><i></i><span>到顶</span></span>
            <div>
                <ul class="chapter-wrap-list normal" ref="scroll" style="height: 350px; overflow-y: scroll; ">
                    <li v-for="(item, index) in items" :key="index" class="bottom-chapter-item">
                        <router-link :to="'/read/' + id + '/' + item.seq" class="chapter-link"
                            :class="{ 'now': item.seq == seq }">

                            <div class="comic-cover">
                                <img :class="{ 'cover-image': true, 'lock': item.lock == 1, 'unLock': item.lock == 2 }"
                                    :src="item.cover">
                            </div>
                            <div class="comic-info">
                                <span class="comic-title">{{ item.title }}</span>
                                <i :class="icon - locate"></i>
                            </div>
                        </router-link>
                    </li>
                </ul>
            </div>
            <div v-if="busy">Loading more items...</div>
            <div v-if="max">少爷，已经到底了...</div>
            <div v-if="err">再试一次...</div>
        </div>

    </section>
    <!-- 提示框 -->
    <modal-confirm v-if="isModalVisible" message="登陆后可使用收藏服务，跳转到登陆页面？" cancel-text="再想想" ok-text="立即登陆" @hide="onModalHide"
        @ok="onModalOk"></modal-confirm>
</template>
<script>
import HotComicSixCmp from "../common/HotComicSixCmpForDetail.vue";
import TopCmp from "../common/TopCmp.vue";
import BackToTopCmp from "../common/BackToTopCmp.vue";
import LoadingCmp from "../common/LoadingCmp.vue";
import apiUtil from "../utils/ApiUtil.vue"
import historyUtil from "../utils/HistoryUtil.vue";
export default {
    data() {
        return {
            id: this.$route.params.id,
            items: [
            ],
            buyList: [],
            isShow: false,
            isAnimate: false,//是否展示更多菜单
            sortAsc: true,//正序排序,
            page: 1,
            busy: false,
            max: false,
            err: false,
            seq: 1,
            fav: false,
            collectionName: "收藏", isModalVisible: false, login: apiUtil.checkLogin()
        }
    }, components: {
        TopCmp, LoadingCmp, HotComicSixCmp, BackToTopCmp
    }, async mounted() {
        //加载详情信1
        await this.loadComicInfo();
        this.$setMetaData(this.title);
        this.loadMoreChapter();
        //关闭加载提示
        this.$refs.loading.hide();
        //绑定滚动事件
        this.bindScrollEvent();

        //查询阅读历史
        var hist = historyUtil.getHistoryByBookId(this.id);
        var idx = 0;//读到第几页
        if (hist != null) {
            this.seq = hist.seq;
            if (hist.index) {
                idx = hist.index;
            }

        }
        //
        //更新历史记录的漫画信息 主要是更新历史记录的更新到第几话
        historyUtil.updateHistory({ "id": this.id, "title": this.title, "artist_name": this.authors[0].name, "lated_seqno": this.updatedChapter, "seq": this.seq, "cover": this.icon, "index": idx });


        //已经读过的要在第一个位置显示
        this.$nextTick(() => {
            const ul = this.$refs.modelChapterList;
            const current = ul.querySelector('.now');
            if (current) {
                ul.scrollLeft = current.offsetLeft;
            }
        })
    }, methods: {
        toggleChapterList() {
            //展开目录
            var tmp = !this.isShow;

            if (tmp) {
                this.isShow = tmp;
                setTimeout(() => {
                    this.isAnimate = true;
                }, 0);
            } else {
                this.isAnimate = false;
                setTimeout(() => {
                    this.isShow = tmp;
                }, 300);
            }
        }
        , sort() {
            this.sortAsc = !this.sortAsc;
            this.page = 1;
            this.items = [];
            this.max = false;
            this.loadMoreChapter();
        },
        //加载漫画详情信息
        async loadComicInfo() {
            var info = {};
            if (apiUtil.checkLogin()) {
                //登陆时携带token
                info = await apiUtil.getComicInfoWithLogin({ 'comicId': this.id });
                await this.loadBuyList();
            } else {
                //未登陆时不携带token
                info = await apiUtil.getComicInfoWithOutLogin({ 'comicId': this.id });
            }

            this.title = info.title;

            this.icon = info.icon;
            this.tags = info.tags;
            this.grade = info.grade;
            this.memberRate = info.memberRate;
            this.desc = info.desc;
            this.authors = info.authors;
            this.status = info.status;
            this.updatedChapter = info.updatedChapter;
            this.tryReadChapter = info.tryReadChapter;


            this.checkLock(info.chapters)
            this.chapters = (info.chapters)





            this.fav = info.fav == 1;
            if (this.fav) {
                this.collectionName = '取消收藏'
            }
            return Promise.resolve();
        },
        async loadMoreChapter() {

            //加载目录
            if (this.busy || this.max) {
                return;
            }
            this.busy = true;
            this.err = false;

            try {
                const data = await apiUtil.loadMoreChapter({ 'page': this.page, 'pageSize': 10, 'sortAsc': this.sortAsc ? 1 : 0 });
                this.max = data.maxPage == this.page;
                this.busy = false;
                this.checkLock(data.items);
                this.items = this.items.concat(data.items);
                this.page++;
            } catch (error) {
                this.busy = false;
                this.err = true;
            }

        },
        async loadBuyList() {
            //实时更新已经购买的章节
            if (!apiUtil.checkLogin()) {
                console.log("no login")
                return;
            }

            const data = await apiUtil.loadChapterBuyList({ 'bookId': this.id });
            this.buyList = this.buyList.concat(data.buyList);
            return Promise.resolve();
        },
        checkLock(items) {
            //校验章节是否购买了
            for (let index = 0; index < items.length; index++) {
                const element = items[index];
                if (element.lock == 1) {
                    //是否解锁
                    element.lock = this.buyList.indexOf(element.seq) > -1 ? 2 : 1;
                }
            }

        }, goTop() {
            //到顶
            this.$refs.scroll.scrollTo({
                top: 0,
                behavior: 'smooth',
            });
        },

        // 绑定滚动事件
        bindScrollEvent() {
            const scrollEl = this.$refs.scroll;
            scrollEl.addEventListener('scroll', () => {
                if (scrollEl.scrollTop + scrollEl.clientHeight >= scrollEl.scrollHeight) {
                    this.loadMoreChapter();
                }
            });
        }, home() {
            this.$router.push("/");
        }, collection() {
            // 收藏
            //没有登陆
            if (!apiUtil.checkLogin()) {
                this.showModal();
                return;
            }

            this.$refs.loading.show();
            //取消收藏
            if (this.fav) {

                apiUtil.delCollection({ ids: [this.id] }).then(() => {
                    this.fav = !this.fav
                    this.$refs.loading.hide();
                    this.collectionName = "收藏"
                })
            } else {
                apiUtil.addCollection({ ids: [this.id] }).then(() => {
                    this.fav = !this.fav
                    this.collectionName = "取消收藏"
                    this.$refs.loading.hide();
                })
            }

        },
        showModal() {
            this.isModalVisible = true;
        },
        onModalHide() {
            console.log("用户取消了操作");
            this.isModalVisible = false;
        },
        onModalOk() {
            console.log("用户确认了操作");
            this.isModalVisible = false;
            this.$router.push("/login")
        }


    }
}
</script>
<style scoped>
.list {
    height: 400px;
    overflow-y: scroll;
    margin-bottom: 20px;
}
</style>