<!-- 广告组件 -->
<template>
    <section class="adv mod-item" data-module-id="middleAd">
        <div class="adv-overflow-block"></div>

        <div class="adv-img">
            <a class="adv-link" @click="handleClick">
                <img class="cover-image" :src="imgSrc" />
            </a>
        </div>
    </section>
</template>
<script>
import apiUtils from '../utils/ApiUtil.vue';
export default {
    data() {
        return {
            link: "",
            imgSrc: "../static/picture/0.png"
        };
    },
    methods: {
        handleClick() {
            // 处理点击事件
            window.location.href = this.link;
        },
        fetchData() {
            //获取首页广告
            apiUtils.getAd().then(data => {
                this.link = data.link;
                this.imgSrc = data.imgSrc;
            })
        }
    }, mounted() {
        this.fetchData();
    }
};
</script>
<style scoped>
/* 新人福利 */
section.adv {
    position: relative;
}

.adv-overflow-block {
    position: absolute;
    top: -0.25rem;
    left: 0;
    background-color: #fff;
    border: 0 solid #fff;
    border-radius: 0.5rem 0.5rem 0 0;
    width: 100%;
    height: 0.65rem;
    z-index: 8;
}

.adv-img {
    margin-left: 0.8rem;
    margin-right: 0.8rem;
    background-color: #FFFFFF;
    overflow: hidden;
}

.adv-img .link {
    display: block;
}

.adv-img .cover-image {
    display: block;
    width: 100%;
}
</style>
