<!-- 搜索结果的列表页 -->
<template>

    <section class="result-box under-top-bar">
        <ul class="result-list" v-if="hasData">
            <li class="comic-item" v-for="(comic, index) in comicList" :key="index">
                <router-link :to="'/detail/' + comic.id" class="comic-link">
                    <div class="comic-cover">
                        <img :src="comic.cover" class="cover-image">
                    </div>
                    <div class="comic-content">
                        <strong class="comic-title">{{ comic.title }}</strong>
                        <small class="comic-update">{{ comic.update }}</small>
                        <small class="comic-tag">{{ comic.tag }}</small>
                        <small class="comic-desc">{{ comic.desc }}</small>
                    </div>
                </router-link>
            </li>
        </ul>
        <div class="not-found-info">
            <!-- 未找到内容 -->
            <div class="not-found-info" v-if="!hasData">
                <div class="npc-box">
                    <i class="npc-not-found"></i>
                    <p class="text-not-found">Sorry，没找到您要的漫画</p>
                    <a class="go-to-index" href="/">好康漫画网</a>
                </div>
            </div>
            <!-- 未找到内容时的推荐组件 -->
            <recoment v-if="!hasData" />
        </div>
    </section>

    <section class="mod-load-more" v-if="hasData">
        <div class="mlm-status-loading" v-bind:style="{ 'display': loading ? 'block' : 'none' }">
            <div class="mlm-dots">
                <span class="mlm-dot"></span>
                <span class="mlm-dot"></span>
                <span class="mlm-dot"></span>
            </div>
            <p class="mlm-info">嘿咻嘿咻加载中</p>
        </div>
        <div class="mlm-status-over" v-bind:style="{ 'display': maxpage ? 'block' : 'none' }">
            大人，已经到底了哦~
        </div>
        <div class="mlm-status-again" v-bind:style="{ 'display': err ? 'block' : 'none' }">
            <a class="mlm-btn-again">
                再刷一次！&gt;_&lt;
            </a>
        </div>
    </section>

</template>


<script>
import apiUtils from '../utils/ApiUtil.vue';
import recoment from './HotSearchCmpWithNoResult.vue';

export default {
    props: ["queryKey"],
    data() {
        return {
            comicList: [],
            loading: false, maxpage: false, err: false, hasData: false,

        };
    }, components: { recoment },
    methods: {
        async getComicData(page = 1, pageSize = 10) {
            //已经达到最大页了
            if (this.maxpage || this.loading) {
                return;
            }
            //设置动效
            this.loading = true;
            this.err = false;

            try {
                const data = await apiUtils.query({ 'page': page, 'pageSize': pageSize, 'key': this.queryKey })
                //关闭动效
                this.loading = false;
                this.comicList = this.comicList.concat( data.data);
                this.currentPage = page;
                //是否最大页
                this.maxpage = this.currentPage == data.maxPage
                this.hasData = !(this.maxpage && this.comicList.length == 0)
            } catch (error) {
                this.err = true;
            }

        },
        handleScroll() {
            const scrollHeight = document.body.scrollHeight;
            const scrollTop = document.body.scrollTop;
            const clientHeight = document.body.clientHeight;
            if (scrollTop + clientHeight >= scrollHeight) {
                this.getComicData(this.currentPage + 1);
            }
        }

    }, mounted() {
        this.getComicData(1);
        window.addEventListener('scroll', this.handleScroll);
    },
    unmounted() {
        window.removeEventListener('scroll', this.handleScroll);
    },
}
</script>

