    background-color: #7a8090;
    border-radius: 50%;
    margin: 0 3px;
}

.mod-load-more .mlm-status-loading p.mlm-info {
    display: inline-block;
    vertical-align: middle;
}

.mod-load-more .mlm-status-loading .mlm-dots span.mlm-dot:nth-of-type(1) {
    background-color: #7a8090;
    -webkit-animation: mod-load-more_dot_move 1s ease-in infinite;
}

.mod-load-more .mlm-status-loading .mlm-dots span.mlm-dot:nth-of-type(2) {
    background-color: rgba(122, 128, 144, 0.6);
    -webkit-animation: mod-load-more_dot_move 1s ease-in 0.3s infinite;
}

.mod-load-more .mlm-status-loading .mlm-dots span.mlm-dot:nth-of-type(3) {
    background-color: rgba(122, 128, 144, 0.3);
    -webkit-animation: mod-load-more_dot_move 1s ease-in 0.7s infinite;
}

