<!-- 热门搜索推荐 -->
<template>
  <section class="search-hot">
    <div class="search-hot-npc">
      <i class="npc-avatar">[NPC头像]</i>
      <span class="npc-dialog">{{ hotSearchTitle }}</span>
    </div>
    <ul class="search-hot-list">
      <li class="item" v-for="item in hotSearchList" :key="item.id">
        <router-link class="link" :to="{ path: '/detail/' + item.id }">{{ item.title }}</router-link>
      </li>
    </ul>
  </section>
</template>

<script>
import apiUtils from '../utils/ApiUtil.vue';

export default {
  data() {
    return {
      hotSearchTitle: "",
        hotSearchList: []
    }
  }, mounted() {
    this.fetch();

  },
  methods: {
    fetch() {
      
      //获取热门搜索提示
      apiUtils.getHotQuery()
        .then(data => {
            this.hotSearchTitle = data.hotSearchTitle;
            this.hotSearchList = data.hotSearchList;

        })
        .catch(error => {
          console.error(error);
        });
    }
  }
}
</script>