<!-- 购买记录 -->
<template>
    <!-- 顶部 -->
    <TopCmp :topTitle="title" />

    <article class="main under-top-bar">
        <div class="mod-his">

            <!-- 数据部分 -->
            <section class="his-normal  " style="display: block;">
                <ul class="his-list" id="his_list">
                    <!-- choose 选择 unChoose 不选择  没有就不选择-->
                    <li :class="['comic-item', item.choose]" v-for="item in historyList" :key="item.id">
                        <router-link :to="'/detail/' + item.id " class="comic-link">
                            <div class="comic-cover">
                                <img class="cover-image" :src="item.cover" :alt="item.artist_name">
                            </div>
                            <div class="comic-info">
                                <strong class="comic-title">{{ item.title }}</strong>
                                <small class="comic-author">作者：{{ item.artist_name || item.artist }}</small>
                                <small class="comic-update">更新到{{ item.lated_seqno || '...' }}话</small>
                                <div class="comic-continue">
                                    <i class="continue-img star"></i>
                                    <small>已购{{ item.seq }}话</small>
                                </div>
                            </div>
                        </router-link>

                        <div class="comic-choose " @click="selectOne(item)">
                            <i class="icon-choose"></i>
                            <i class="icon-unChoose"></i>
                        </div>
                    </li>
                </ul>
            </section>


        </div>


    </article>


    <!-- 提示组件 -->
    <TipCmp ref="tip" />
    <!-- 返回顶部 -->
    <BackToTopCmp />
</template>
<script>

import TopCmp from "../common/TopCmp.vue"
import TipCmp from "../common/ListTipCmp.vue"
import BackToTopCmp from "../common/BackToTopCmp.vue";
import apiUtils from "../utils/ApiUtil.vue";
export default {
    data() {
        return {
            title: "已购章节",
            historyList: [],

        }
    },
    methods: {
        async getComicData(page = 1, pageSize = 10) {

            //已经达到最大页了
            if (!this.$refs.tip.canScroll()) {
                return;
            }
            // //设置动效
            this.$refs.tip.loadingStart();

            try {

                var data = await apiUtils.getBuyList({ 'page': page, 'pageSize': pageSize })

                this.historyList = this.historyList.concat(data.list);
                this.currentPage = page;
                //是否最大页
                this.$refs.tip.loadingEnd(this.currentPage == data.maxPage, this.historyList.length > 0)
            
            } catch (error) {
                this.$refs.tip.hasError();
            }

        },
        handleScroll() {
            const scrollHeight = document.body.scrollHeight;
            const scrollTop = document.body.scrollTop;
            const clientHeight = document.body.clientHeight;
            if (scrollTop + clientHeight >= scrollHeight) {
                this.getComicData(this.currentPage + 1);
            }
        }



    },
    components: {
        TopCmp, TipCmp, BackToTopCmp
    }, mounted() {
        this.$setMetaData("已购章节");
        this.getComicData(1);
        window.addEventListener('scroll', this.handleScroll);
    },
    unmounted() {
        window.removeEventListener('scroll', this.handleScroll);
    },
}
</script>
<style scoped>

.main .mod-his .his-normalfuck {
    background-color: #F5F5EE;

    padding-top: 1px;
    padding-bottom: 1px;
}

</style>