<!-- 漫画列表页 -->
<template>
    <!-- 顶部 -->
    <TopCmp :topTitle="categoryName" />
    <!-- Tab页 -->
    <section class="categoryList-tab under-top-bar">
        <div class="tab-list">
            <a id="tab_update" v-bind:class="{ 'tab-list-item': true, 'update': true, 'active': tab == 1 }"
                @click="tabClick(1)">更新</a>
            <a id="tab_hot" v-bind:class="{ 'tab-list-item': true, 'hot': true, 'active': tab == 2 }"
                @click="tabClick(2)">人气</a>
            <a id="tab_fav" v-bind:class="{ 'tab-list-item': true, 'fav': true, 'active': tab == 3 }"
                @click="tabClick(3)">收藏</a>
        </div>
    </section>
    <!-- 数据列表 -->
    <section class="categoryList-content content-hot">
        <div id="list_hot_box" class="comic-list hot active">
            <ul id="list_hot">
                <li class="comic-item" v-for="(comic, index) in comicList" :key="index">
                    <router-link :to="'/detail/' + comic.id" class="comic-link">
                        <div class="comic-cover">
                            <img class="cover-image" :src="comic.coverImage">
                        </div>
                        <div class="comic-info">
                            <strong class="comic-title">{{ comic.title }}</strong>
                            <small class="comic-update">{{ comic.update }}</small>
                            <small class="comic-hot">人气：{{ comic.hot }}</small>
                            <small class="comic-fav">收藏数：{{ comic.fav }}</small>
                            <small class="comic-tag">{{ comic.tag }}</small>
                            <small class="comic-desc">{{ comic.desc }}</small>
                        </div>
                    </router-link>
                </li>
            </ul>
        </div>
    </section>
    <!-- 提示组件 -->
    <TipCmp ref="tip" />
    <!-- 返回顶部 -->
    <BackToTopCmp />
</template>
<script>

import TopCmp from "../common/TopCmp.vue"
import TipCmp from "../common/ListTipCmp.vue"
import BackToTopCmp from "../common/BackToTopCmp.vue";

import apiUtils from "../utils/ApiUtil.vue";
export default {
    //分类名字 和分类id
    data() {
        return {
            categoryName: this.$route.params.categoryName,
            categoryId: this.$route.params.categoryId,
            tab: 1,// 1 更新 2 人气 3 收藏 。默认更新,
            comicList: []

        }
    },
    components: {
        TopCmp, TipCmp, BackToTopCmp
    }, methods: {
        //切换tab
        tabClick(t) {
            if (this.tab == t) {
                return;
            }
            this.tab = t;
            this.comicList = [];
            this.$refs.tip.reset();
            this.getComicData(1)
        },

        async getComicData(page = 1, pageSize = 10) {
            //已经达到最大页了
            if (!this.$refs.tip.canScroll()) {
                return;
            }
            //设置动效
            this.$refs.tip.loadingStart();

            try {
                const response = await apiUtils.getCommicList({ 'page': page, 'pageSize': pageSize, 'category': this.categoryId, 'tab': this.tab });
                this.comicList = this.comicList.concat(response.data);
                this.currentPage = page;
                //是否最大页
                this.$refs.tip.loadingEnd(this.currentPage == response.maxPage, this.comicList.length > 0)
            } catch (err) {
                this.$refs.tip.hasError();
            }
        },
        handleScroll() {
            const scrollHeight = document.body.scrollHeight;
            const scrollTop = document.body.scrollTop;
            const clientHeight = document.body.clientHeight;
            if (scrollTop + clientHeight >= scrollHeight) {
                this.getComicData(this.currentPage + 1);
            }
        }

    }, mounted() {
        this.$setMetaData("列表");
        this.getComicData(1);
        window.addEventListener('scroll', this.handleScroll);
    },
    unmounted() {
        window.removeEventListener('scroll', this.handleScroll);
    },
}
</script>
