<!-- 推荐分类列表 -->
<template>
    <section class="category-strong under-top-bar redbg">
        <ul class="category-strong-ul myul">
            <li class="strong-ul-item" v-for="(item, index) in categories" :key="index">
                <router-link :to="'/comicList/' + item.title + '/' + item.id" class="strong-ul-item-link">
                    <strong>{{ item.title }}</strong>
                    <small>{{ item.subtitle }}</small>
                </router-link>
            </li>
        </ul>
    </section>
</template>
  
<script>
import apiUtils from '../utils/ApiUtil.vue';

export default {
    data() {
        return {
            categories: []
        };
    },
    created() {
        this.loadData();
    },
    methods: {
        loadData() {
            apiUtils.getRecommendCat().then(data => {
                this.categories = data;
            });
        }
    }
};
</script>
