<script>
import axios from 'axios';
import historyUtil from './HistoryUtil.vue';
const baseUrl = ""
// 创建一个 axios 实例 用于鉴权
const api = axios.create({
    baseURL: baseUrl,
    timeout: 10000,
    withCredentials: true, // 允许发送跨域 Cookie
});

// 创建一个 axios 实例 无需鉴权
const noAuthApi = axios.create({
    baseURL: baseUrl,
    timeout: 10000,
    withCredentials: true, // 允许发送跨域 Cookie
});

// 添加请求拦截器
api.interceptors.request.use((config) => {
    // 在发送请求前检查用户是否已经登录，可以从 cookie 或 localStorage 中获取登录状态
    const isLoggedIn = checkUserLoginStatus(); // 自定义函数，用于检查用户登录状态
    if (isLoggedIn) {
        // 如果用户已经登录，则添加 token 到请求头中
        const token = getToken(); // 自定义函数，用于获取用户登录后的 token
        config.headers.Authorization = `Bearer ${token}`;
    } else {
        // 如果用户未登录，则重定向到登录页面
        redirectToLoginPage(); // 自定义函数，用于跳转到登录页面
    }
    return config;
});

api.interceptors.response.use(
    response => {
        // 处理响应数据
        const data = response.data;
        if (data && data.code === 200) {
            return data.data;
        } else if (data.code == 404) {
            console.log(baseUrl + response.config.url)
            const url = new URL(baseUrl + "/" + response.config.url);
            const bookId = url.searchParams.get('bookId');
            const seq = url.searchParams.get('seq');
            return window.location.href = '/#/err/' + bookId + '/' + seq;// 重定向到登录页
        } else {
            console.error('Response error:', data);
            return Promise.reject(new Error(data.message));
        }
    },
    error => {
        // 处理响应错误
        console.error('Response error:', error);
        return Promise.reject(error);
    }
);


noAuthApi.interceptors.response.use(
    response => {
        // 处理响应数据
        const data = response.data;
        if (data && data.code === 200) {
            return data.data;
        } else if (data.code == 404) {
            const url = new URL(baseUrl + "/" + response.config.url);
            const bookId = url.searchParams.get('bookId');
            const seq = url.searchParams.get('seq');
            return window.location.href = '/#/err/' + bookId + '/' + seq;// 重定向到登录页
        } else {
            console.error('Response error:', data);
            return Promise.reject(new Error(data.message));
        }
    },
    error => {
        // 处理响应错误
        console.error('Response error:', error);
        return Promise.reject(error);
    }
);


function checkUserLoginStatus() {
    const isLoggedIn = window.localStorage.getItem('isLoggedIn'); // 从 localStorage 中获取登录状态

    return isLoggedIn === 'true';
}



function getToken() {
    const token = localStorage.getItem('token'); // 尝试从本地存储中获取 token
    if (token) {
        return token; // 如果找到 token，则返回它
    } else {
        return null; // 如果找不到 token，则返回 null
    }
}

function redirectToLoginPage() {
    window.location.href = '/#/login' // 重定向到登录页
}




//不带缓存的api()
function noCacheApi(axObj, api, options) {
    return axObj.get(api, options).then(response => {
        return Promise.resolve(response);
    });
}

// 带有缓存的api
function cacheApi(axObj, api, options, cacheTime = 1000) {
    //const cacheTime = 1000 * 60 * 24;//缓存毫秒

    const cacheKey = `${api}_${JSON.stringify(options)}`;
    const cachedData = localStorage.getItem(cacheKey);
    if (cachedData) {

        const { createTime, response } = JSON.parse(cachedData);
        const now = new Date().getTime();
        if (now - createTime <= cacheTime) {
            console.log("find cache", api)
            return Promise.resolve(response);
        }
    }
    return axObj.get(api, options).then(response => {
        console.log("insert cache", api)
        const createTime = new Date().getTime();
        const cacheData = JSON.stringify({ createTime, response });
        localStorage.setItem(cacheKey, cacheData);
        return Promise.resolve(response);
    });
}


// 封装各种后端 API 接口的调用方法
const apiUtils = {
    //获取用户信息
    getUserInfo: () => api.get('/assets/userInfo.json'),
    //获取banner信息
    getBanner: () => cacheApi(noAuthApi, "/assets/banner.json"),
    //获取seo相关信息
    getSeo: () => cacheApi(noAuthApi, "/assets/seo.json"),
    //获取ad配置
    getAd: () => cacheApi(noAuthApi, "/assets/ad.json"),
    //3:1 4图推荐
    getHot4: () => noCacheApi(noAuthApi, "/assets/hotComic3.json"),
    //2:2 4图推荐
    getHot4_2: () => noCacheApi(noAuthApi, "/assets/hotComic2.json"),
    //获取6图热门推荐
    get6Hot: () => noCacheApi(noAuthApi, "/assets/hotComic.json"),
    //详情页的推荐数据
    get6HotForDetail: () => noCacheApi(noAuthApi, "/assets/hotForDetailComic.json"),
    //查询分类菜单
    getCat: () => cacheApi(noAuthApi, "/assets/categoryData.json"),
    //获取推荐分类
    getRecommendCat: () => cacheApi(noAuthApi, "/assets/recommendCategoryData.json"),
    //查询漫画列表
    getCommicList: (data) => noCacheApi(noAuthApi, `/assets/queryListData.json?page=${data.page}&pageSize=${data.pageSize}&category=${data.category}&tab=${data.tab}`),
    //查询排行榜
    getRank: (data) => cacheApi(noAuthApi, `/assets/rankData.json?page=${data.page}&pageSize=${data.pageSize}&type=${data.type}`),
    //查询收藏列表
    getCollection: (data) => noCacheApi(api, `/assets/collectionData.json?page=${data.page}&pageSize=${data.pageSize}`),
    //删除收藏
    delCollection: (data) => noCacheApi(api, `/assets/collectionData.json?ids=${data.ids}`),
    //添加收藏
    addCollection: (data) => noCacheApi(api, `/assets/collectionData.json?ids=${data.ids}`),
    //同步历史记录
    getHistory: () => noCacheApi(api, `/assets/historyJson.json`),
    //提交历史记录 修改为post
    pushHistory: (data) => api.get("/assets/pushHistory.json", { 'data': data }),
    //没有内容时的推荐
    getRecommendHotSearch: () => cacheApi(noAuthApi, '/assets/hotSearchDataWithNoResult.json'),
    //智能搜索
    smartQuery: (data) => cacheApi(noAuthApi, `/assets/smartSearch.json?query=${data.key}`),
    //推荐搜索
    getHotQuery: () => cacheApi(noAuthApi, `/assets/hotSearchData.json`),
    //执行搜索
    query: (data) => cacheApi(noAuthApi, `/assets/searchResult.json?page=${data.page}&pageSize=${data.pageSize}&query=${data.key}`),
    //帮助列表
    getHelp: (data) => cacheApi(noAuthApi, `/assets/helpListData.json?page=${data.page}&pageSize=${data.pageSize}`),
    //帮助详情
    getHelpDetail: (data) => cacheApi(noAuthApi, `/assets/helpDetailData.json?qid=${data.qid}`),
    //帮助内容反馈
    feedBack: (data) => noCacheApi(noAuthApi, `/assets/feedback.php?qid=${data.qid}&good=${data.good}`),
    //登录相关。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。
    getCode: (data) => noCacheApi(noAuthApi, `/assets/code.json?phone=${data.phone}`),
    //登录 对接服务器后使用post
    login: (data) => noAuthApi.get('/assets/login.json', { 'data': data }),//deng
    //注册
    register: (data) => noAuthApi.get(`/assets/register.json `, { 'data': data }),
    //设置密码
    setPwd: (data) => api.get(`/assets/setPwd.json`, { 'data': data }),
    //获取购买记录
    getBuyList: (data) => api.get(`/assets/buyList.json?page=${data.page}&pageSize=${data.pageSize}`),
    //获取充值订单列表 充值记录
    getOrderList: (data) => api.get(`/assets/orderList.json?page=${data.page}&pageSize=${data.pageSize}`),
    //校验是否存在未支付的订单
    checkOrder: () => api.get(`/assets/checkOrder.json`),
    //获取没有存在的订单详情
    getOrderInfo: () => api.get(`/assets/getOrderInfo.json`),
    //  获取目录列表
    loadMoreChapter: (data) => cacheApi(noAuthApi, `/assets/chapter.json?page=${data.page}&pageSize=${data.pageSize}&asc=${data.sortAsc}`),
    //获取章节列表 登陆
    loadReadChapters: (data) => cacheApi(noAuthApi, `/assets/chapterOnReadPage.json?page=${data.page}&pageSize=${data.pageSize}&asc=${data.sortAsc}`),
    //不登陆获取章节列表
    loadReadChaptersWithLogin: (data) => cacheApi(api, `/assets/chapterOnReadPage.json?page=${data.page}&pageSize=${data.pageSize}&asc=${data.sortAsc}`),
    //获取章节购买列表
    loadChapterBuyList: (data) => api.get(`/assets/chapterBuyList.json?bookId=${data.bookId}`),
    //详情页获取漫画信息 需要登陆
    getComicInfoWithLogin: (data) => cacheApi(api, `assets/comicInfo.json?bookId=${data.comicId}`),
    //详情页获取漫画信息，不需要登陆
    getComicInfoWithOutLogin: (data) => cacheApi(noAuthApi, `assets/comicInfo.json?bookId=${data.comicId}`),
    //阅读页面获取漫画信息
    loadComic: (data) => cacheApi(noAuthApi, `assets/loadComic.json?bookId=${data.comicId}`),
    //加载图片内容  需要登陆
    loadComicContent: (data) => cacheApi(api, `assets/comicContent.json?bookId=${data.comicId}&seq=${data.seq}`),
    //加载图片内容 不需要登陆
    loadComicContentWithOutLogin: (data) => cacheApi(noAuthApi, `assets/comicContent.json?bookId=${data.comicId}&seq=${data.seq}`),
    //购买章节
    buy: (data) => api.get(`assets/buy.json?bookId=${data.comicId}&seq=${data.seq}`),
    //校验是否登陆
    checkLogin: () => checkUserLoginStatus(),
    //登陆
    setLogin: (token) => {
        localStorage.setItem('token', token);
        window.localStorage.setItem('isLoggedIn', true)
        //登录的时候同步历史记录
        historyUtil.syncHistory();
    },
    //登出
    logout: () => {
        //登出之前 提交历史记录
        historyUtil.pushHistory().then(() => {
            localStorage.setItem('token', '');
            window.localStorage.setItem('isLoggedIn', false);
        })
    }
};
export default apiUtils;

</script>
