<!-- 热门6图 推荐模块推荐 -->
<template>

    <section class="hot-comic mod-item" v-if="show">
        <h2 class="sub-title">
            <strong class="title-content"> {{ titleContent }}</strong>
        </h2>

        <ul class="comic-list col-3 recommend-list">
            <li class="comic-item" v-for="(comic, index) in comics" :key="index">
                <a class="comic-link" href="javascript:;" :data-id="comic.id" :data-pos="index + 1"
                    @click="fuckClick(comic.id)">
                    <div class="comic-cover">
                        <img class="cover-image" :src="comic.imageSrc">
                    </div>
                    <div class="comic-content">
                        <strong class="comic-title">{{ comic.title }}</strong>
                        <small class="comic-desc">{{ comic.description }}</small>
                    </div>
                </a>
            </li>
        </ul>

        <div class="btn-replacement-data" @click="refreshData">[换一批]</div>
    </section>
</template>

<script >
import apiUtils from '../utils/ApiUtil.vue';
export default {
    data() {
        return {
            titleContent: "",
            comics: [],
            show: false
        }
    },
    mounted() {
        this.fetchData();
    },
    methods: {
        fetchData() {
            
            // Make a call to get the data (titleContent and comics) from the JSON using an HTTP client such as axios
            // axios.get('/api/hot-comics')
            apiUtils.get6Hot()
                .then(data => {
                    // Set the titleContent and comics properties with the data obtained from the JSON
                    this.titleContent = data.titleContent;
                    this.comics = data.comics;
                    this.show = true;

                })
                .catch(error => {
                    console.error(error);

                });
        },
        refreshData() {
            this.fetchData();
        }, fuckClick(id) {
            this.$router.push("/detail/" + id);
        }
    }
};
</script>
