<!-- 漫画阅读页面 -->
<template>
    <!-- 头部悬浮栏区域 hidden -->
    <section class="float-bar top" :class="{ 'hidden': hidden }">
        <a class="float-bar-btn back" @click="back">[返回]</a>
        <label class="float-bar-title">
            <span class="title">{{ title }}}</span>
            <span class="progress">第{{ seq }}话 - {{ seq }}/{{ total }}</span>
        </label>
        <router-link :to="'/detail/' + id" class="float-bar-btn detail">
            <i class="icon icon-detail"></i>
        </router-link>
        <router-link to="/" class="float-bar-btn home">
            <i class="icon icon-home"></i>
        </router-link>
    </section>
    <!-- 底部 -->
    <section class="float-bar bottom" :class="{ 'hidden': hidden }">
        <a class="float-bar-btn chapter-list" @click="showlChapterList">[目录]</a>
        <a class="float-bar-btn prev-chapter" v-if="seq > 1" @click="gotoNext(-1)">[上一章]</a>
        <a class="float-bar-btn next-chapter" v-if="total > seq" @click="gotoNext(1)">[下一章]</a>
    </section>

    <!-- 悬浮在右侧的作品目录信息 show animate -->
    <section class="float-chapter-list  " :class="{ 'show': show, 'animate': animate }">
        <div class="fixed-chapter-list-mask" @click="showlChapterList"></div>
        <div class="chapter-list-rank">
            <h2 class="comic-title">
                {{ title }} <span style="font-size: 12px">({{ status }})</span>
            </h2>
            <div class="chapter-list-desc">
                共{{ total }}话 (可试读至{{ tryNumber }}话)
                <span class="max-free-hint"></span>
                <span class="list-order " :class="{ 'asc': chapterOrderAsc, 'desc': !chapterOrderAsc }">
                    <i class="icon-order"></i>
                    <span @click="sort">{{ chapterOrderAsc ? "正序" : "倒叙" }}</span>
                </span>
            </div>
            <div class="chapter-list-wrap">
                <ul class="list-wrap normal active" ref="scroll">
                    <span v-if="busy">加载中请稍后</span>
                    <li v-for="(chapter, index) in chapters" :key="index" class="chapter-item">
                        <a class="chapter-link  "
                            :class="{ 'lock': chapter.lock == 1, 'unLock': chapter.lock == 2, 'now': chapter.seq == seq }"
                            @click="go2Chapter(chapter)">
                            <p class="chapter-title">{{ chapter.seq }} - {{ chapter.title }}</p>
                        </a>
                    </li>
                </ul>

            </div>
            <div class="icon-locate icon-back-to-top to-top" id="top_or_bottom" @click="goTop"></div>
            <div class="chapter-list-count">
                <span class="count">共{{ total }}话</span>
            </div>
        </div>
    </section>

    <!-- 漫画图片区域 -->
    <section class="comic-pic-area" style="height:  100vh; overflow: auto; background-color: black;" @scroll="handleScroll"
        ref="scrollContainer">
        <!-- 浏览控制按键 - 上一页 -->
        <a class="nav-ctrl-btn prev" v-if="seq > 1" @click="gotoNext(-1)">点击加载上一章节</a>
        <a class="nav-ctrl-btn prev" v-if="seq <= 1">前面没有更多了</a>
        <!-- 章节图片列表loading -->
        <div class="loading bounce comic-pic-list-loading" :style="{ 'display': loading }">
            <div class="item"></div>
            <div class="item"></div>
            <div class="item"></div>
        </div>
        <!-- 画面内容 -->
        <section class="comic-pic-list-all" ref="list" @click="clickComic">
            <ul class="comic-pic-list" v-for="seq in seqs" :key="seq.index">
                <ComicPicItem v-for="pic in seq.pics" :key="pic.index" :index="pic.index" :data-timeout="pic.timeout"
                    :image-url="pic.url" :seq="seq.seq" :total="seq.pics.length"
                    :scroll-to-me="pic.index === currentIndex" />
            </ul>
        </section>

        <!-- 浏览控制按键 - 下一页 -->
        <a class="nav-ctrl-btn next" v-if="seq >= total">已经没有了</a>

        <!-- 浏览模式提示 -->
        <div class="view-mode-hint">123213</div>
        <!-- 进度框 -->
        <div class="progress-box" :class="{ 'out': !hidden }">{{ seq }}话 {{ Number(currentIndex) + 2 }}/{{ totalCount }}
        </div>
    </section>
    <!-- 提示框 -->
    <modal-confirm v-if="isModalVisible" :message="message" cancel-text="再想想" :ok-text="okMsg" @hide="onModalHide"
        @ok="onModalOk"></modal-confirm>
    <!-- 提示 -->
    <div class="pile-dialog-wrap toast " :class="{ open: showScrollTips }" style="z-index: 1000010;">
        <div class="dialog-box  " :class="{ show: showScrollTips }" style="z-index: 1000012;">

            <div class="dialog-content">
                <p class="dialog-para">{{ smallTip }}</p>
            </div>
        </div>
    </div>

    <div class="modal-blocker-wrap  " :class="{ 'show': needBuy }" id="modal_block61677139897289">
        <div class="modal-blocker-mask show"></div>
        <div class="modal-blocker-container show">

            <div class="blocker-content">
                <div class="content">购买：第{{ seq }}话 {{ seqTitle }}</div>
                <div class="content">
                    <p style="font-size:0.7rem;">价格：<span style="color:#FF613E;">{{ price }}</span> <span
                            style="color:#999;">点券</span><i style="display:inline-block;width:24px;"></i>余额：{{ balance }}
                        <span style="color: #999;">点券</span>
                        <small class="weak small" v-if="price > balance">(点券不足)</small>
                    </p>
                </div>
            </div>
            <div class="blocker-btn-group">
                <router-link to="/pay" class="btn btn-ok" v-if="price > balance">去充值点券</router-link>
                <a class="btn btn-ok" v-if="price <= balance" @click="buythis">点击购买</a>
                <!-- <a href="https://www.baidu.com" class="btn btn-ok">观看广告解锁</a> -->
                <router-link :to="'/detail/' + this.id" class="btn btn-cancel">暂不充值，返回</router-link>
            </div>
        </div>
    </div>
</template>
<script>
import apiUtils from '../utils/ApiUtil.vue';
import ComicPicItem from './ComicImgCmp.vue';
import historyUtil from '../utils/HistoryUtil.vue';
export default ({
    data() {
        return {
            id: this.$route.params.id,
            seq: this.$route.params.seq,//这是第几章
            isModalVisible: false,//显示模态提示框
            message: "登陆后可以使用购买服务，是否前往登陆页？",
            okMsg: "立即登陆",
            chapters: [],
            chapterOrderAsc: true,
            page: 0,//目录分页
            busy: false, //是否加载目录
            max: false, //目录最大页
            err: false,//目录加载错误
            show: false, animate: false, hidden: false, //菜单显示
            showScrollTips: false,
            smallTip: '',//小提示
            loading: 'block',//加载提示,
            seqs: [],//漫画内容信息
            currentIndex: 0,//当前章读到第几页了 从0开始,
            totalCount: 0,//当前章总页数量,


        }
    }, components: {
        ComicPicItem
    },
    async beforeMount() {
        return Promise.resolve();
    },
    async mounted() {

        //加载漫画信息
        await this.loadComic();
        //加载书
        await this.loadComicContent(true);
        //加载历史
        await this.loadHistory();
        //跳转到index页面
        if (!this.needBuy && this.currentIndex > 0) {
            //滑动到已经看到的页面
            this.goToCurrent();
        }
        //设置seo
        this.$setMetaData(this.title);
        //绑定滚动事件
        this.bindScrollEvent();
        return Promise.resolve();

    },
    beforeUnmount() {
        //目录滚动事件
        this.$refs.scroll.removeEventListener("scroll", this.loadMoreChapter);
    }
    ,
    methods: {

        handleScroll() {

            const scrollEl = this.$refs.scrollContainer;
            const idx = this.getTopItemIndex(scrollEl);
            const index = scrollEl.getElementsByClassName('comic-pic-item')[idx].getAttribute('index');
            if (this.currentIndex != index) {

                this.currentIndex = index;
                var seq = scrollEl.getElementsByClassName('comic-pic-item')[idx].getAttribute('seq');
                if (this.seq != seq) {
                    this.seq = seq;
                    this.totalCount = scrollEl.getElementsByClassName('comic-pic-item')[idx].getAttribute('total');
                }
                // 加入到历史记录
                historyUtil.addHistory({ "id": this.id, "title": this.title, "artist_name": this.authorName, "lated_seqno": this.total, "seq": this.seq, "cover": this.icon, "index": this.currentIndex })
            }
            // console.log('已经到达顶部', scrollEl.scrollTop, ":::", scrollEl.scrollHeight, ":  :", scrollEl.clientHeight);
            // 判断是否到达底部
            if (scrollEl.scrollTop + scrollEl.clientHeight + 1 >= scrollEl.scrollHeight) {
                if (this.seq < this.total) {
                    //向下加载一章
                    this.gotoNext(1)
                }
            }

            // 判断是否到达顶部
            if (scrollEl.scrollTop <= 0) {
                if (this.seq > 1) {
                    //向上加载一章
                    this.gotoNext(-1)
                }
            }
        },
        clickComic() {
            //点击漫画区域 打开关闭菜单
            this.hidden = !this.hidden;
            //关闭提示
            this.showScrollTips = false;
        }
        ,
        getTopItemIndex(scrollEl) {
            //获取当前的位置
            const scrollTop = scrollEl.scrollTop + scrollEl.clientHeight / 3;
            const listItems = scrollEl.getElementsByClassName('comic-pic-item');

            let left = 0;
            let right = listItems.length - 1;

            while (left <= right) {
                const mid = Math.floor((left + right) / 2);
                const offsetTop = listItems[mid].offsetTop;

                if (offsetTop < scrollTop) {
                    left = mid + 1;
                } else if (offsetTop > scrollTop) {
                    right = mid - 1;
                } else {
                    return mid;
                }
            }

            return left > 0 ? left - 1 : 0;
        },
        back() {
            this.$router.push("/detail/" + this.id)
        }, go2Chapter(chapter) {
            //关闭目录
            this.showlChapterList();
            this.seq = chapter.seq;
            this.currentIndex = 0;
            this.seqs = [];//清空当前的数据，
            this.loadComicContent(true)
        },
        showModal(msg) {
            this.message = msg
            this.isModalVisible = true;
        }, showSmallTips(msg) {
            this.smallTip = msg;
            this.showScrollTips = true;

        },
        onModalHide() {
            console.log("用户取消了操作");
            this.isModalVisible = false;
        },
        onModalOk() {
            console.log("用户确认了操作");
            this.isModalVisible = false;
            this.$router.push("/login")
        },
        sort() {
            if (this.busy) {
                return;
            }
            this.chapterOrderAsc = !this.chapterOrderAsc;
            this.page = 1;
            this.max = false;
            this.chapters = [];
            this.loadMoreChapter();
        }
        , async loadMoreChapter() {
            //加载目录
            if (this.busy || this.max) {
                return;
            }
            this.busy = true;

            try {
                var data = {};
                if (apiUtils.checkLogin()) {
                    data = await apiUtils.loadReadChaptersWithLogin({ 'page': this.page, 'pageSize': 10, 'sortAsc': this.chapterOrderAsc ? 1 : 0 });
                } else {
                    data = await apiUtils.loadReadChapters({ 'page': this.page, 'pageSize': 10, 'sortAsc': this.chapterOrderAsc ? 1 : 0 });
                }

                this.max = data.maxPage == this.page;
                this.busy = false;
                this.chapters = this.chapters.concat(data.chapters);
                this.page++;
                //已经读过的要在第一个位置显示
                this.$nextTick(() => {
                    const list = this.$refs.scroll.children;
                    for (let i = 0; i < list.length; i++) {
                        const item = list[i];
                        if (item.querySelector('.now')) {
                            item.scrollIntoView({ block: 'start', behavior: 'smooth' });
                            break;
                        }
                    }
                })

            } catch (error) {
                this.busy = false;
            }
        }, goTop() {
            //目录到顶方法
            this.$refs.scroll.scrollTo({
                top: 0,
                behavior: 'smooth',
            });
        }, goToCurrent() {
            //第一次加载后，要跳转到上次看到的图片
            // 如果当前传入的pid不是第一张图片的pid，那么需要先滑动到对应的图片位置
            if (this.currentIndex != 0) {
                const ulList = this.$refs.list.querySelectorAll('.comic-pic-list');
                const ul = ulList[0];
                const list = ul.children
                var img = list[this.currentIndex]
                //提示打开
                this.showSmallTips('已经为主人跳到上次看到的位置~');
                img.scrollIntoView();
            }
        }
        // 绑定滚动事件
        , bindScrollEvent() {
            //目录滚动事件
            const scrollEl = this.$refs.scroll;
            scrollEl.addEventListener('scroll', () => {
                if (scrollEl.scrollTop + scrollEl.clientHeight >= scrollEl.scrollHeight) {
                    this.loadMoreChapter();
                }
            });


        }, showlChapterList() {
            //开关目录列表
            //展开目录
            var tmp = !this.show;

            if (tmp) {
                if (this.page == 0) {
                    this.page++;
                    this.loadMoreChapter();
                }

                this.show = tmp;
                setTimeout(() => {
                    this.animate = true;
                }, 0);
            } else {
                this.animate = false;
                setTimeout(() => {
                    this.show = tmp;
                }, 300);
            }
        }, async gotoNext(num) {
            //上一章、下一章
            this.seq = Number(this.seq) + num;
            this.currentIndex = 0;
            await this.loadComicContent(num > 0)

        }, async loadComic() {

            //加载当前漫画数据
            const comicInfo = await apiUtils.loadComic({ "comicId": this.id });
            this.title = comicInfo.title;
            this.authorName = comicInfo.authorName;//作者
            this.icon = comicInfo.icon;//图标
            this.status = comicInfo.status;
            this.total = comicInfo.total;
            this.tryNumber = comicInfo.tryNumber;
            this.loading = 'none';//关闭加载提示,


            return Promise.resolve();
        }, async loadComicContent(concat) {
            this.loading = 'block';
            var content = {}
            //根据登陆情况查询章节内容
            if (apiUtils.checkLogin()) {
                content = await apiUtils.loadComicContent({ "comicId": this.id, "seq": this.seq });
            } else {
                content = await apiUtils.loadComicContentWithOutLogin({ "comicId": this.id, "seq": this.seq });
            }
            //根据方向插入新内容
            if (concat) {
                this.seqs.push(content.chapters);
            } else {
                this.seqs.unshift(content.chapters);
            }
            //清空一些内容
            if (this.seqs.length > 50) {
                if (concat) {
                    // 释放最前面的元素
                    this.seqs.shift();
                } else {
                    // 释放最后面的元素
                    this.seqs.pop();
                }
            }
            this.totalCount = content.count;//当前章总页数量,
            this.seqTitle = content.seqTitle;//当前章节的标题
            this.price = content.price;//章节价格
            this.balance = content.balance;//携带用户的余额。如果未登陆就是0
            this.needBuy = content.lock == 1;// 章节是否需要购买 0 否1 是 2已购买
            this.loading = 'none';
            return Promise.resolve();
        }, async loadHistory() {
            //加载历史记录
            const hist = historyUtil.getHistoryByBookId(this.id);
            if (hist == null) {
                this.currentIndex = 0;
            } else {
                if (hist.index && hist.seq == this.seq) {

                    this.currentIndex = hist.index;
                } else {
                    this.currentIndex = 0
                }
            }
            return Promise.resolve();
        }, async buythis() {
            try {
                if (this.busy) {
                    return;
                }
                this.loading = 'block';
                this.busy = true;
                const data = await apiUtils.buy({ 'comicId': this.id, 'seq': this.seq });
                if (data.status == 1) {
                    this.showSmallTips("购买成功！")
                    this.seqs = [];
                    this.loadComicContent(true);
                    this.needBuy = false;

                } else {
                    this.showSmallTips(data.msg);
                }

                this.busy = false;
                this.loading = 'none';
            } catch (error) {
                this.showSmallTips("系统繁忙，请稍后重试");
                this.busy = false;
                this.loading = 'none';
            }

        }

    }, created() {
        setInterval(() => {
            this.hidden = true;
            this.showScrollTips = false;
        }, 3500);
    }
})

</script>

 