<!-- 漫画图片组件 -->
<template>
    <li :class="['comic-pic-item', { 'pic-unloaded': !loaded }, { 'pic-loaded': loaded }]" :index="index" :seq="seq"
        :total="total">

        <div class="comic-pic-pad" :style="{ paddingTop: padTop }">
            <div class="err-info" v-if="loadError">
                <span>嗷呜~图片加载失败咯</span>
                <a class="reload-btn" @click="reloadImage">重新加载</a>
            </div>
        </div>
        <div class="comic-pic-box">
            <img class="comic-pic" :key="imageKey" :src="imageUrl" @load="onImageLoad" @error="onImageError"
                :data-timeout="imageTimeout" ref="image" />
        </div>
    </li>
</template>
<script>
export default {
    props: {
        //第几页
        index: {
            type: Number,
            required: true,
        },
        //第几章
        seq: {
            type: String,
            required: true,
        },
        timeout: {
            type: Number,
            default: 0,
        },
        imageUrl: {
            type: String,
            required: true,
        }, scrollToMe: {
            type: Boolean,
            default: false,
        }, total: {
            type: Number,
            default: 0,
        }
    },
    data() {
        return {
            padTop: "110%",
            imageTimeout: 2000,
            loadError: false,
            loaded: false,
            imageKey: 0
        };
    },
    methods: {
        onImageLoad() {
            this.padTop = `${(this.$refs.image.clientHeight / this.$refs.image.clientWidth) * 100}%`;
            this.loaded = true;
            this.imageKey = Date.now();
            
        },
        onImageError() {
            this.loadError = true;
        },
        reloadImage() {
            this.loadError = false;
            // 图片加载错误时，将key的值加1，触发重新加载
            this.imageKey += 1;
        },
    },
};
</script>