<template>
    <header :class="{ 'top-bar': true, 'no-title': false }">
        <a class="btn-top back" @click="back">返回</a>
        <h1 class="top-title">{{ title }}</h1>
        <div class="padding"></div>
    </header>


    <article class="main under-top-bar">
        <div class="mod-his">
            <!-- 数据部分 -->
            <section class="his-normal  " style="display: block;">
                <ul class="his-list" id="his_list" style=" padding-top: 10px;">
                    <!-- choose 选择 unChoose 不选择  没有就不选择-->
                    <li class='comic-item'>
                        <a class="comic-link">
                            <div class="comic-info">
                                <strong>{{ answer }}</strong>
                                <img :src="item.src" v-for="item in imgs" :key="item.id">
                            </div>
                        </a>
                    </li>
                </ul>
            </section>

        </div>



        <div class="faq-vote ">
            <span>
                <div class="vote-icon">
                    <img @click="feedback(true)"
                        src="data:image/png;base64,"
                        alt="good">
                </div>
                <div v-if="feedBacks"> 很棒，解决了我的问题 </div>
                <div v-if="good"> 感谢您的反馈 </div>
            </span>
            <span>
                <div class="vote-icon"><img @click="feedback(false)"
                        src="data:image/png;base64,"
                        alt="bad"></div>
                <div v-if="feedBacks"> 不是我想要的答案 </div>
                <div v-if="bad"> 感谢您的反馈 </div>
            </span>
        </div>

        <div class="mint-cell-value"><span></span></div>

        <section class="faq-remark"><span>仍无法解决您的问题？点击<a @click="submit" class="">提交反馈</a></span></section>




    </article>

    <!-- 提示框 -->
    <modal-confirm v-if="isModalVisible" message="联系qq管理员解决您的问题" cancel-text="取消" ok-text="打开qq" @hide="onModalHide"
        @ok="onModalOk"></modal-confirm>
</template>
<script>
import apiUtils from '../utils/ApiUtil.vue';
export default {
    data() {
        return {
            title: "帮助与反馈",
            questionTitle: " ",
            answer: "",
            imgs: []
            ,
            isModalVisible: false,
            feedBacks: true,
            good: false,
            bad: false
        }
    }, mounted() {
        this.$setMetaData("帮助与反馈");
        this.getComicData()
    },
    methods: {
        async getComicData() {

            var qid = this.$route.params.qid;


            var data = await apiUtils.getHelpDetail({ 'qid': qid })
            //获取远程数据。包括是否反馈、反馈结果等
            this.title = data.title
            this.answer = data.answer
            this.imgs = data.imgs
            this. feedBacks = !data. feedBacks
            this.good = data.good
            this.bad = !data.good

        }, back() {
            this.$router.back();
        }, submit() {
            this.showModal();
        },
        showModal() {
            this.isModalVisible = true;
        },
        onModalHide() {
            console.log("用户取消了操作");
            this.isModalVisible = false;
        },
        onModalOk() {
            console.log("用户确认了操作");
            this.isModalVisible = false;
        }, feedback(f) {
            if (!this.feedBacks) {
                return;
            }
            var qid = this.$route.params.qid;
            apiUtils.feedBack({ 'qid': qid, 'good': f });
            this.feedBacks = false;
            this.bad = !f;
            this.good = f;
            //更新缓存

        }
    }
}
</script>

<style scoped>
.faq-vote {
    position: relative;
    display: -webkit-flex;
    display: flex;
    padding-bottom: 12px;
    -webkit-justify-content: space-around;
    justify-content: space-around;
    margin: 20px auto 0;
    color: #b9b9b9;
}

.faq-vote span {
    min-width: 120px;
    text-align: center;
    margin-top: 16px;
}

.faq-vote .vote-icon {
    margin-bottom: 10px;
    opacity: .3;
}

img {
    max-width: 100%
}


.faq-remark {
    margin: 20px 0;
    text-align: center;
    font-size: 14px;
    line-height: 20px;
    color: #c6c6c6;
}

.faq-remark a {
    color: #4fabf5;
}
</style>