<!-- 注册页面 -->
<template>
    <div id="__layout">
        <div id="app">
            <div class="login">
                <div class="header">
                    <div class="back" @click="back"></div>
                    <img src="../../assets/Logo2.png">
                </div>
                <div class="content">
                    <div class="input phone">
                        <div class="menu">+86 <i class="arrow"></i></div>
                        <input type="number" placeholder="请输入手机号" @change="change" v-model="phoneNumber" />
                    </div>

                    <div class="input code" v-if="!usePwd">
                        <!-- 提示验证码倒计时 -->
                        <div class="countdown" v-if="countDown < 60">{{ countDown }}s</div>
                        <!-- 获取验证码的按钮 -->
                        <div class="btn" @click="getCode" v-if="countDown == 60">{{ btnText }}</div>
                        <!-- 输入框 -->
                        <input @input="change" type="text" placeholder="请输入验证码" v-model="code" />
                    </div>

                    <div class="agreement"><i :class="{ 'checked': checked }" @click="checkProtocol"></i>
                        我已阅读并同意 <a href="userPrivacyPolicy.html">《牛逼漫画隐私协议》</a></div>

                    <!-- .user-info-success  可用 否则不可用 -->
                    <div class="btn " :class="{ 'user-info-success': canSubmit }" @click="login">下一步</div>


                </div>
                <!-- 错误提示 -->
                <div :class="{ 'message-pop': true, 'hide': !isShow }" v-show="isShow">
                    <div class="content">
                        {{ message }}
                    </div>
                </div>


            </div>
        </div>
    </div>


</template>

<script>
import apiUtils from '../utils/ApiUtil.vue';
export default {
    data() {
        return {
            phoneNumber: "",
            code: "",
            timer: null,
            countDown: 60,
            btnText: "获取验证码",
            isShow: false,
            message: '',
            canSubmit: false,
            usePwd: false,
            checked: false
        };
    },
    methods: {
       async getCode() {

            const reg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
            if (reg.test(this.phoneNumber)) {
                if (this.countDown === 60) {
                    this.countDown = 59
                  
                    // mock API call to get code
                    await apiUtils.getCode({ "phone": this.phoneNumber });
                    this.startCountDown();
                   
                } else {
                    console.log("fuck man")
                }
            } else {
                console.error('请输入正确的手机号码');
                this.showErrorTip('请输入正确的手机号码');
            }

        },
        startCountDown() {
            this.timer = setInterval(() => {
                this.countDown--;
                if (this.countDown === 0) {
                    clearInterval(this.timer);
                    this.countDown = 60;
                }
            }, 1000);
        },
        login() {
            if (!this.checked) {
                this.showErrorTip("确认已经阅读隐私协议")
            }

            if(!this.canSubmit){
                return;
            }
            this.canSubmit = false;

            apiUtils.register({ 'phone': this.phoneNumber, 'code': this.code }).then(data=>{
                if(data.status!=1){
                    this.showErrorTip(data.msg);
                }else{
                    //登录  
                    apiUtils.setLogin(data.token);
                    this.$router.push("/registeStep2/" + this.phoneNumber)
                }
            })
           
         
        },
        showErrorTip(message) {
            //错误提示
            this.isShow = true
            this.message = message
            setTimeout(() => {
                this.hideErrorTip()
            }, 3000)
        },
        hideErrorTip() {
            //隐藏错误信息
            this.isShow = false
            this.message = ''
        }, change() {
            const reg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
            this.canSubmit = reg.test(this.phoneNumber) && this.code.length >= 6 && this.checked
        }, changeLoginType() {
            //切换登录方式：使用密码或者手机验证码
            this.usePwd = !this.usePwd;
        }, back() {
            this.$router.back()
        }, checkProtocol() {
            this.checked = !this.checked;
            this.change();
        }

    },mounted(){
        this.$setMetaData("注册新用户");
    }
};
</script>

