<!-- 热门1:3图 推荐模块推荐 -->
<template>

    <section class="youth-comic mod-item" v-if="show">
        <h2 class="sub-title">
            <strong class="title-content"> {{ titleContent }}</strong>
        </h2>

        <ul class="comic-list col-1-3 recommend-list">
            <li v-for="(comic, index) in comics" :key="index"
                :class="['comic-item', index == 0 ? 'comic-item-1' : 'comic-item-1-3', `item-${index + 1}`]">
                <a class="comic-link" href="javascript:;" :data-id="comic.id" :data-pos="index + 1"
                    @click="fuckClick(comic.id)">
                    <div class="comic-cover">
                        <img class="cover-image" :src="comic.imageSrc">
                    </div>
                    <div class="comic-content">
                        <strong class="comic-title">{{ comic.title }}</strong>
                        <small class="comic-desc">{{ comic.description }}</small>
                    </div>
                </a>
            </li>
        </ul>

        <div class="btn-replacement-data" @click="refreshData">[换一批]</div>
    </section>
</template>

<script >
import apiUtils from '../utils/ApiUtil.vue';
export default {
    data() {
        return {
            titleContent: "",
            comics: [],
            show: false
        }
    },
    mounted() {
        this.fetchData();
    },
    methods: {
        fetchData() {
            apiUtils.getHot4()
                .then(response => {
                    // Set the titleContent and comics properties with the data obtained from the JSON
                    this.titleContent = response.titleContent;
                    this.comics = response.comics;
                    this.show = true;

                })
                .catch(error => {
                    console.error(error);

                });
        },
        refreshData() {
            this.fetchData();
        }, fuckClick(id) {
          this.$router.push("/detail/"+id)
        }

    }
};
</script>
