<!-- 没有搜索结果时的推荐内容 -->
<template>
  <div class="recommend-box">
    <h2 class="recommend-title" >{{ hotSearchTitle }}</h2>
    <ul class="recommend-list no-tag ">
      <li class="comic-item" v-for="(item, index) in hotSearchList" :key="index">
        <router-link :to="`/detail/${item.id}`" class="comic-link">
        <div class="comic-cover">
            <img class="cover-image fuckimg" :src="item.cover">
          </div>
          <div class="comic-content">
            <strong class="comic-title">{{ item.title }}</strong>
            <small class="comic-desc">{{ item.desc }}</small>
          </div>
        </router-link>
      </li>
    </ul>
</div>
</template>

<script>
import apiUtils from '../utils/ApiUtil.vue';
export default {
    data() {
      return {
        hotSearchTitle: "",
          hotSearchList: []
      }
    }, mounted() {
      this.fetch();
    },
    methods: {
      fetch() {
        
        //获取热门搜索提示
        apiUtils.getRecommendHotSearch()
          .then(data => {
               
              this.hotSearchTitle = data.hotSearchTitle;
              this.hotSearchList = data.hotSearchList;
  
          })
          .catch(error => {
            console.error(error);
          });
      }
    }
};
</script>
