<!-- 登陆页面 -->
<template>
    <div id="__layout">
        <div id="app">
            <div class="login">
                <div class="header">
                    <div class="back" @click="back" ></div>
                    <img src="../../assets/Logo2.png">
                </div>
                <div class="content">
                    <div class="input phone">
                        <div class="menu">+86 <i class="arrow"></i></div>
                        <input type="number" placeholder="请输入手机号" @change="change" v-model="phoneNumber" />
                    </div>

                    <div class="input code" v-if="!usePwd">
                        <!-- 提示验证码倒计时 -->
                        <div class="countdown" v-if="countDown < 60">{{ countDown }}s</div>
                        <!-- 获取验证码的按钮 -->
                        <div class="btn" @click="getCode" v-if="countDown == 60">{{ btnText }}</div>
                        <!-- 输入框 -->
                        <input @input="change" type="text" placeholder="请输入验证码" v-model="code" />
                    </div>

                    <div class="input code" v-if="usePwd">

                        <!-- 输入框 -->
                        <input @input="change" type="text" placeholder="请输入密码" v-model="code" />
                    </div>

                    <!-- .user-info-success  可用 否则不可用 -->
                    <div class="btn " :class="{ 'user-info-success': canSubmit }" @click="login">登录</div>
                    <p class="new-user">
                        <span @click="changeLoginType" v-if="!usePwd" >账号密码登录<i></i></span>
                        <span @click="changeLoginType" v-if="usePwd" >验证码登陆<i></i></span>
                        <span   @click="register" >新用户注册<i></i></span>
                    </p>
                    <div class="other-login">
                        <h3 class="title">
                            <i class="left"></i> <span class="text">其他方式登录</span>
                            <i class="right"></i>
                        </h3>
                        <div class="list">
                            <div class="qq" @click="otherLogin('qq')" >
                                <img src="../../assets/image/more_weibo.png" />
                                <span>QQ</span>
                            </div>
                            <div class="weixin" @click="otherLogin('weChat') ">
                                <img src="../../assets/image/more_weixin.png" />
                                <span>微信</span>
                            </div>
                            <div class="weibo"  @click="otherLogin('weibo') ">
                                <img src="../../assets/image/more_weibo.png" />
                                <span>微博</span>
                            </div>
                        </div>
                    </div>
                    <p class="protocol">
                        - 登录即代表你同意
                        <a href="userPrivacyPolicy.html">隐私协议</a>
                        和
                        <a href="userProtocol.html">用户服务协议</a> -
                    </p>
                </div>
                <!-- 错误提示 -->
                <div :class="{ 'message-pop': true, 'hide': !isShow }" v-show="isShow">
                    <div class="content">
                        {{ message }}
                    </div>
                </div>


            </div>
        </div>
    </div>


</template>

<script>
import apiUtils from '../utils/ApiUtil.vue';
export default {
    data() {
        return {
            phoneNumber: "",
            code: "",
            timer: null,
            countDown: 60,
            btnText: "获取验证码",
            isShow: false,
            message: '',
            canSubmit: false,
            usePwd: false
        };
    },
    methods: {
        async getCode() {

            const reg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
            if (reg.test(this.phoneNumber)) {
                if (this.countDown === 60) {
                    this.countDown = 59;
                    // mock API call to get code
                    await apiUtils.getCode({ 'phone': this.phoneNumber });
                    this.startCountDown();
                } else {
                    console.log("fuck man")
                }
            } else {
                console.error('请输入正确的手机号码');
                this.showErrorTip('请输入正确的手机号码');
            }

        },
        startCountDown() {
            this.timer = setInterval(() => {
                this.countDown--;
                if (this.countDown === 0) {
                    clearInterval(this.timer);
                    this.countDown = 60;
                }
            }, 1000);
        },
        login() {
            if(!this.canSubmit){
                return;
            }

            this.canSubmit = false;
            // mock API call to login
            apiUtils.login({"phone":this.phoneNumber,"code":this.code,"type":this.usePwd}).then(data=>{
                if(data.status !=1){
                    this.showErrorTip(data.msg);

                }else{
                    //存储数据
                    apiUtils.setLogin(data.token);
                    this.$router.push("/userCenter")
                }
            });

        },
        showErrorTip(message) {
            //错误提示
            this.isShow = true
            this.message = message
            setTimeout(() => {
                this.hideErrorTip()
            }, 3000)
        },
        hideErrorTip() {
            //隐藏错误信息
            this.isShow = false
            this.message = ''
        }, change() {
            const reg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
            this.canSubmit = reg.test(this.phoneNumber) && this.code.length >= 6
        }, changeLoginType() {
            //切换登录方式：使用密码或者手机验证码
            this.usePwd = !this.usePwd;
        },back(){
            this.$router.push("/");
        },otherLogin(type){
            this.showErrorTip("暂不支持"+type+"登陆")
        },register(){
            //跳转到注册页面
            this.$router.push("/registe")
        }

    },mounted(){
        this.$setMetaData("登陆");
        if(apiUtils.checkLogin()){
            this.$router.push("/");
        }
    }
};
</script>

